import React from "react";

const Contact = () => (


  <div className="contact">

  {/*  CONTACT PAGE 'HEADER'  */}
  <div>
    <h3 class="contact-linear-wipe">Call me, let's chat</h3>
  </div>

    {/*  CONTACT OPTIONS/ICONS  */}
    <div>
      <p className="contact-button">
        <a href="mailto:davidapears@gmail.com?subject=Contact from davidapears.com">
          <img className="contact-icon" src="/images/email.png" alt="e-mail link" width="75" height="75"></img>
        </a>
        <a href="https://www.github.com/davidapears" target="_blank">
          <img className="contact-icon" src="/images/github.png" alt="github link" width="75" height="75"></img>
        </a>
        <a href="https://www.linkedin.com/in/davepears/" target="_blank">
          <img className="contact-icon" src="/images/linkedin.png" alt="linkedin link" width="75" height="75"></img>
        </a>
        <a href="https://www.twitter.com/davidapears" target="_blank">
          <img className="contact-icon" src="/images/twitter.png" alt="twitter link" width="75" height="75"></img>
        </a>
        <a href="https://www.instagram.com/david.pears">
          <img className="contact-icon" src="/images/igram.png" alt="instagram link" width="75" height="75"></img>
        </a>
        <a href="skype:davidapears?call" target="_blank">
          <img className="contact-icon" src="/images/skype.png" alt="skype link" width="75" height="75"></img>
        </a>
        <p> <a href="tel:447815087757" class="phone">📞 +44 (0) 7815 087757</a> </p>
        </p>
    </div>

    {/*  CV DOWNOAD OPTION  */}
    <div className="cvdownload">
      <p>Download my CV</p>
        <a href="./images/cv/DavidPearsCVFeb2019.pdf" download>
          <img src="./images/cvdownload.png" alt="David's CV" width="60" height="60"></img>
        </a>
    </div>
  </div>
);

export default Contact;



