import React from "react";

const CurriculumVitae = () => (

<div className="curriculum-vitae">

	{/* MAIN WINDOW CONTAINER (I.E BLACK BOX WITH 'FAKE CODE' IN IT)*/}
	<section class="window">

		{/* TOP BAR OF 'FAKE CODE', Coloured circles and title */}
		<div class="window-top">
			<div class="window__controls">
				<span></span>
				<span></span>
				<span></span>
			</div>
			<div class="window__title">david.pears.html - Curriculum Vitae
			</div>
		</div>

		{/* MAIN BODY OF FAKE CODE */}
		<div class="window-main">
			<ol>
				<li>&lt;!<span class="text">doctype html</span>&gt;</li>
				<li>&lt;<span class="tag">html</span>&gt;</li>
	     <br></br>

				<li>&nbsp;&nbsp;&lt;<span class="tag">head</span>&gt;</li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span class="tag">title</span>&gt;<span class="text">David's CV...</span>&lt;<span class="tag">/title</span>&gt;</li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span class="tag">link</span> <span class="attr">href</span>=<span class="val">"styles/style.css"</span> <span class="attr">rel</span>=<span class="val">"stylesheet"</span>&gt;</li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;<span class="comment">&lt;!-- A work in progress --&gt;</span></li>
				<li>&nbsp;&nbsp;&lt;/<span class="tag">head</span>&gt;</li>
      <br></br>

        <li>&nbsp;&nbsp;&lt;<span class="tag">head</span>&gt;</li>
  			<li>&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span class="tag">title</span>&gt;<span class="text"> DOWNLOAD MY CV </span>&lt;<span class="tag">/title</span>&gt;</li>
  			<li>&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span class="tag">link</span> <span class="attr">href</span>=<span class="val">"images/DavidPearsCVFeb2019.pdf</span> <span class="attr">rel</span>=<span class="val">"cv"</span>&gt;</li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;<span className="CVlink"><a className="CVlink" href="./images/cv/DavidPearsCVFeb2019.pdf" download>&lt;!--  CLICK HERE --&gt;</a></span></li>
				<li>&nbsp;&nbsp;&lt;/<span class="tag">head</span>&gt;</li>
      <br></br>

				<li>&nbsp;&nbsp;&lt;<span class="tag">body</span>&gt;</li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;<span class="comment">&lt;!-- Did you see the download link above? --&gt;</span></li>
				<li>&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span class="tag">script</span> <span class="attr">src</span>=<span class="val">"scripts/script.js</span>&gt;&lt;/<span class="tag">script</span>&gt;</li>
				<li>&nbsp;&nbsp;&lt;/<span class="tag">body</span>&gt;</li>
        <br></br>

				<li>&lt;/<span class="tag">html</span>&gt;</li>
        <li>&nbsp;&nbsp;&nbsp;&nbsp;<span class="comment">&lt;!-- CLICK YELLOW LINK FOR CV PDF --&gt;</span></li>
			</ol>
		</div>

	</section>

	<div className="CV-instruction">
		<p> Click on yellow link to download CV <br></br>or head to <a href="https://www.linkedin.com/in/davepears/" target="new">LinkedIn</a></p>
	</div>

</div>




);

export default CurriculumVitae;



