import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => (


  <div>
      <div className="menu">
        <input id="menu--toggle"
               type="checkbox" />

        <label className="menu--toggle__trigger"
               htmlFor="menu--toggle"></label>

        <label className="menu--toggle__burger"
               htmlFor="menu--toggle"></label>

      <ul className="menu__body">
        <li className="menu__body-element">
          <a className="menu__body-link"href="/">
            Home
          </a>
        </li>
        <li className="menu__body-element">
          <a className="menu__body-link" href="/about">
            About
          </a>
        </li>
        <li className="menu__body-element">
          <a className="menu__body-link"href="/projects">
            Projects
          </a>
        </li>
        <li className="menu__body-element">
          <a className="menu__body-link"href="/curriculumvitae">
            Curriculum Vitae
          </a>
        </li>
        <li className="menu__body-element">
          <a className="menu__body-link"href="/contact">
            Contact
          </a>
        </li>
      </ul>
    </div>
  </div>
);

export default Navbar;


